import React from 'react';
import './App.scss';
import { Person } from './components/Person/Person';

// eslint-disable-next-line no-unused-vars
const misha = {
  name: 'Misha',
  age: 37,
  sex: 'm',
  isMarried: true,
  partnerName: 'Natasha',
};

// eslint-disable-next-line no-unused-vars
const olya = {
  name: 'Olya',
  sex: 'f',
  isMarried: true,
  partnerName: 'Maksym',
};

// eslint-disable-next-line no-unused-vars
const alex = {
  name: 'Alex',
  age: 25,
  sex: 'm',
  isMarried: false,
};

const people = [misha, olya, alex];

const App = () => (
  <div className="App">
    {people.map(person => (
      <Person
        name={person.name}
        age={person.age}
        isMarried={person.isMarried}
        partnerName={person.partnerName}
        sex={person.sex}
      />
    ))}
  </div>
);

export default App;
